import React, { useEffect } from 'react';
import { useState } from 'react';
import axios from 'axios'
import './Reg.css';

function App() {
  const [username,setusername]=useState("");
  const [password,setpassword]=useState("");
  const [country,setcountry]=useState("");
  const [email,setemail]=useState("");
  const [contact,setContact]=useState("");


const submitfun=async (e)=>{
e.preventDefault();
 await axios.post("/api/v1/register",{username,password,country,email,contact});
localStorage.setItem("name",username)
  alert("User Registered Successfully")

  window.location.reload();
  
}
  return (
  <>
 {/* Container */}
<div className="container mx-auto">
  <div className="flex justify-center items-center h-screen px-6">
    {/* Row */}
    <div className="w-full xl:w-3/4 lg:w-11/12 flex">
      {/* Col */}
      <div className="w-full h-auto bg-gray-400 hidden lg:block lg:w-5/12 bg-cover rounded-l-lg" style={{backgroundImage: 'url("https://source.unsplash.com/Mv9hjnEUHR4/600x800")'}} />
      {/* Col */}
      <div className="w-full lg:w-7/12 bg-white p-5 rounded-lg lg:rounded-l-none" style={{boxShadow:"5px 5px grey"}}>
        <h3 className="pt-4 text-2xl text-center" style={{fontWeight:"bold"}}>Create an Account!</h3>

        <form className="px-8 pt-6 pb-8 mb-4 bg-white rounded" onSubmit={submitfun}>
          <div className="mb-4 md:flex md:justify-between">
            <div className="mb-4 md:mr-2 md:mb-0">
              <label className="block mb-2 text-sm font-bold text-gray-700" htmlFor="Username">
                Username
              </label>
              <input className="w-full px-3 py-2 text-sm leading-tight text-gray-700 border rounded shadow appearance-none focus:outline-none focus:shadow-outline" id="Username" type="text" placeholder="Username"
              onChange={(e)=>setusername(e.target.value)}
              />
            </div>
            <div className="md:ml-2">
              <label className="block mb-2 text-sm font-bold text-gray-700" htmlFor="Country">
              Country
              </label>
              <input className="w-full px-3 py-2 text-sm leading-tight text-gray-700 border rounded shadow appearance-none focus:outline-none focus:shadow-outline" id="country" type="text" placeholder="country"
                onChange={(e)=>setcountry(e.target.value)}
              />
            </div>
          </div>
          <div className="mb-4">
            <label className="block mb-2 text-sm font-bold text-gray-700" htmlFor="Email">
  Email
            </label>
            <input className="w-full px-3 py-2 mb-3 text-sm leading-tight text-gray-700 border rounded shadow appearance-none focus:outline-none focus:shadow-outline" id="email" type="email" placeholder="Email"
              onChange={(e)=>setemail(e.target.value)}
            />
          </div>
          <div className="mb-4 md:flex md:justify-between">
            <div className="mb-4 md:mr-2 md:mb-0">
              <label className="block mb-2 text-sm font-bold text-gray-700" htmlFor="Contact">
                Contact
              </label>
              <input className="w-full px-3 py-2 mb-3 text-sm leading-tight text-gray-700 border border rounded shadow appearance-none focus:outline-none focus:shadow-outline" id="Contact" type="Contact" placeholder="Contact"
                onChange={(e)=>setContact(e.target.value)}
              />
      
            </div>
            <div className="md:ml-2">
              <label className="block mb-2 text-sm font-bold text-gray-700" htmlFor="Password">
                Password
              </label>

              <input className="w-full px-3 py-2 mb-3 text-sm leading-tight text-gray-700 border border-red-500 rounded shadow appearance-none focus:outline-none focus:shadow-outline" id="password" type="password" placeholder="******************"
                onChange={(e)=>setpassword(e.target.value)}
              />
              	{/* <p className="text-xs italic text-red-500">password must be required</p> */}
            </div>
          </div>
         
            <button className="w-full px-4 py-2 font-bold text-white bg-blue-500 rounded-full hover:bg-blue-700 focus:outline-none focus:shadow-outline" type="Submit">
              Register Account
            </button>
          
          <hr className="mb-6 border-t" />
        
          
        </form>
      </div>
    </div>
  </div>
</div>

  
  
  </>
  );
}

export default App;