import React, { useEffect } from 'react'
import styled from 'styled-components'
import { useGlobalContext } from '../../context/globalContext';
import History from '../../History/History';
import { InnerLayout } from '../../styles/Layouts';
import { dollar } from '../../utils/Icons';
import Chart from '../Chart/Chart';
import "./Dash.css"
function Dashboard() {
    const {totalExpenses,incomes, expenses, totalIncome, totalBalance, getIncomes, getExpenses } = useGlobalContext()

    useEffect(() => {
        getIncomes()
        getExpenses()
    }, [])

    return (
        <>
            <InnerLayout>
                <h1 style={{fontFamily:"cursive" ,fontWeight:"bold"}} >All Transactions</h1>

               <div className='up'>
                <div className='left'>
                <Chart />
                </div>
               <div className='right' style={{fontFamily:"cursive" ,fontWeight:500}}>
                <History />
               </div>
               
               </div>



                    <div className="chart-con">

                        <div className="amount-con">
                            <div className="income">
                                <h2>Total Income</h2>
                                <p>
                                    {dollar} {totalIncome()}
                                </p>
                            </div>
                            <div className="income">
                                <h2>Total Expense</h2>
                                <p>
                                    {dollar} {totalExpenses()}
                                </p>
                            </div>
                            <div className="income">
                                <h2>Total Balance</h2>
                                <p>
                                    {dollar} {totalBalance()}
                                </p>
                            </div>
                        </div>
                   
                    <div className="history-con">
                        
                        <h2 className="salary-title">Min <span style={{fontFamily:"cursive" ,fontWeight:"bold"}}>Salary</span>Max</h2>
                        <div className="salary-item">
                            <p>
                                ${Math.min(...incomes.map(item => item.amount))}
                            </p>
                            <p>
                                ${Math.max(...incomes.map(item => item.amount))}
                            </p>
                        </div>
                        <h2 className="salary-title">Min <span style={{fontFamily:"cursive" ,fontWeight:"bold"}}>Expense</span>Max</h2>
                        <div className="salary-item">
                            <p>
                                ${Math.min(...expenses.map(item => item.amount))}
                            </p>
                            <p>
                                ${Math.max(...expenses.map(item => item.amount))}
                            </p>
                        </div>
                    </div>

                    </div>
              
                
            </InnerLayout>
        </>
    )
}

